
import axios from 'axios'
import Vue from 'vue';
import ViewBox from 'vux/src/components/view-box/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Popup from 'vux/src/components/popup/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XHeader from 'vux/src/components/x-header/index.vue'

Vue.component(Popup.name, Popup);
export default {
data(){
return {
contents:{rights:'评分说明',title:'遵纪守法评分'},
list:[
],
isYellow:false,
showPop:false,
uuserId:''
}
}, filters: {


Upper: function (value) {
try {
if(value==="N") throw "去处理";
if(value==="Y")  throw "已审核";
if(value=== 0)  throw "待审核";
}
catch(err) {
return value=err;
}
},
Upper1: function (value) {
try {
/* if(value===null) throw "yellowA";*/
if(value==="N")  throw "yellowB";
if(value==="Y")  throw "yellowC";
}
catch(err) {
return value=err;
}

}
},
components:{
XHeader,
ViewBox,
Popup,
Flexbox,
FlexboxItem
},
directives:{
TransferDom
},
methods:{


getlist(){
axios({
method: 'get',
url: 'pscoreparty/getDakDetialByDepartmentId',
params: {
/*  departmentId:1*/
departmentId:this.$store.getters.user.departmentid

}
}) .then((res)=> {
this.list=res.data;
})
.catch(function (error) {
console.log(error);
});
},

changeItem(item){
if(item.validYn=="Y"){
this.$router.push({
path: 'points/lawAbidingDetailFinish/:userId',
name: 'lawAbidingDetailFinish',
params: {
userId: item.userId
}
})

}else {
this.$router.push({
path: 'lawAbidingDetail/:userId',
name: 'lawAbidingDetail',
params: {
userId: item.userId
}
})
}
},
showMenu(){
this.showPop = true;
},
know(){
this.showPop = false
},
},
mounted() {
this.getlist()
}




}



